import { Box, Button, Container, Input } from '@mui/material'
import React, { useEffect, useState,useRef } from 'react'
import '../Styles/styles.css'
import SendIcon from '@mui/icons-material/SendOutlined';
import { Chats, Navbar, Welcome, } from '../index';
import { logo, user } from '../../utils/Constant'
import { FetchFromAPI } from '../../utils/FetchFromAPI';
const ChatRoom = () => {
    const [input, setInput] = useState('')
    const [message, setMessage] = useState('')
    const [chatLog, setChatLog] = useState([]);
    const [placeholder, setPlaceholder] = useState('')
    const [isLight, setIsLight] = useState(false)
    const messageEndRef = useRef(null);
    useEffect(() => {
        setInput('')
        FetchFromAPI(message)
            .then((data) => {
                {
                    
                    !message ?  setPlaceholder('Please enter input...') :
                        setChatLog([...chatLog,
                        {
                            user: 'zChat',
                            query: data.choices[0].text.slice(2)
                        }
                        ]);
                }
            })
    }, [message])
    useEffect(() => {
      messageEndRef.current?.scrollIntoView();
    }, [chatLog])
    
    setTimeout(() => {
        setPlaceholder('')
    }, 5000);
    return (
        <>
            <Navbar 
                setChatLog={setChatLog}
                setIsLight={setIsLight}
                isLight={isLight}
            />
            <Container
                sx={{
                    backgroundColor: isLight?'white':'#343541',
                    display: 'flex',
                    flex: 1,
                    maxWidth: { md: '100%' },
                    padding: 0,
                    position: 'relative',
                    justifyContent: 'center',
                    alignItems: { md: 'flex-start', xs: 'flex-start' },
                }}
            >
                <Box
                    sx={{
                        display: 'flex',
                        backgroundColor: 'transparent',
                        margin: '3% 0',
                        height: { md: '90%' },
                        flex: '1',
                        width: '100%',
                        maxHeight: { md: '85vh', xs: '91vh' },
                    }}
                >

                    <Box
                        sx={{
                            display: 'flex',
                            overflow: 'auto',
                            flex: 1,
                            flexDirection: 'column',
                            color: isLight?'black':'white',
                        }}
                    >
                        {

                            !chatLog.length ? <Welcome /> :
                                chatLog.map((query, index) => (
                                    <Chats
                                        message={query.query}
                                        key={index}
                                        bgColor={
                                            query.user === 'zChat' && 'rgb(68 70 84)'
                                        }
                                        logo={
                                            query.user === 'zChat' ? logo : ''
                                        }
                                        avtarBG={
                                            query.user === 'zChat' ? 'cyan' : 'transparent'
                                        }
                                    />
                                ))
                        }
                        <div ref={messageEndRef}></div>
                    </Box>
                </Box>
                <Box
                    sx={{
                        position: 'fixed',
                        bottom: '3%',
                        width: {md:'60%',xs:'90%'},
                        boxShadow: '0 0 10px #202123',
                        borderRadius: '5px',
                        backgroundColor: isLight?'white':'#3435416e',
                    }}
                >
                    <Box
                        sx={{
                            position: 'relative',
                        }}
                    >
                        <Input
                            placeholder={
                                placeholder
                            }
                            onChange={(e) => setInput(e.target.value)}
                            value={input}
                            color='error'
                            inputProps={{
                                border: 'none',
                                outline: 'none',
                                color: isLight?'black':'white',
                            }}
                            sx={{
                                // position: 'fixed',
                                border: 'none',
                                outline: 'none',
                                borderRadius: '5px',
                                width: '100%',
                                padding: '2px 5px',
                                fontSize: '18px',
                                color: isLight?'black':'white',
                            }}
                        />
                        <Button
                            onClick={() => {
                                // e.preventDefault();
                                setMessage(input)
                                input === '' ?
                                    setPlaceholder('Please enter input...') :
                                    setChatLog([...chatLog,
                                    {
                                        user: 'me',
                                        query: input
                                    }
                                    ]);
                            }}
                            
                            sx={{
                                position: 'absolute',
                                right: '0px',
                                padding: 'auto 0',
                                justifyContent: 'center',
                            }}
                        >
                            <SendIcon
                                sx={{
                                    color: isLight?'black':'white',
                                    
                                }}
                            />
                        </Button>
                    </Box>
                </Box>
            </Container>
        </>
    )
}

export default ChatRoom
